
import {
  ref,
  defineComponent,
  computed,
  onMounted,
  watch
} from 'vue';
import { messageProps } from './messageProps';
import { MessageType } from './token';
export default defineComponent({
  name: 'BsMessage',
  props: messageProps,
  emits: ['destroy'],
  setup (props: any, ctx) {
    let visible = ref(false);
    let timer: number;

    let customStyle = computed(function () {
      return {
        top: props.offsetTop + 'px',
        zIndex: props.zIndex
      };
    });

    let clearTimer = function () {
      clearTimeout(timer);
    };

    let startTimer = function () {
      clearTimer();
      if (props.duration > 0) {
        timer = setTimeout(close, props.duration);
      }
    };

    let close = function () {
      visible.value = false;
      clearTimer();
      if (typeof props.onClose === 'function') {
        props.onClose();
      }
    };

    // message 一显示出来则立即开启倒计时
    watch(visible, function (newVal: boolean) {
      if (newVal) {
        startTimer();
      }
    });
    watch(() => props.repeatNum, function () {
      startTimer();
    });

    onMounted(function () {
      visible.value = true;
    });
    return {
      visible,
      customStyle,

      close,
      clearTimer,
      startTimer
    };
  }
});
